import React from 'react';
const Like = ({ liked, onClick }) => {
  let classes = 'fa fa-heart';
  if (!liked) classes += '-o';
  return (
    <i
      onClick={onClick}
      style={{ cursor: 'pointer', color: 'pink' }}
      className={classes}
      aria-hidden="true"
    />
  );
};
export default Like;
